import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useAuth } from "@/provider";
import { addToast } from "@heroui/toast";
import DefaultLayout from "@/layouts/default";

export default function LoginPage() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const { login } = useAuth();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!username || !password) {
      addToast({
        title: "Error",
        description: "Please enter both username/email and password.",
        color: "danger",
      });
      return;
    }
    try {
      await login(username, password);
      navigate("/notes");
    } catch (err: any) {
      addToast({
        title: "Error",
        description: err?.response?.data?.message || "Login failed. Please try again later.",
        color: "danger",
      });
    }
  };

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-200/60 to-purple-200/60">
        <div className="glass p-8 w-full max-w-md shadow-lg">
          <h2 className="text-2xl font-bold mb-6 text-center">Sign In</h2>
          <form onSubmit={handleSubmit} className="flex flex-col gap-4">
            <input
              type="text"
              placeholder="Username or Email"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-blue-400"
              value={username}
              onChange={e => setUsername(e.target.value)}
              autoComplete="username"
            />
            <input
              type="password"
              placeholder="Password"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-blue-400"
              value={password}
              onChange={e => setPassword(e.target.value)}
              autoComplete="current-password"
            />
            <button
              type="submit"
              className="w-full py-2 mt-2 rounded bg-blue-500 hover:bg-blue-600 text-white font-semibold shadow"
            >
              Sign In
            </button>
          </form>
          <div className="mt-4 text-center text-sm text-gray-700">
            Don't have an account?{' '}
            <Link to="/register" className="text-blue-600 hover:underline">Sign Up</Link>
          </div>
        </div>
      </div>
    </DefaultLayout>
  );
} 