import { useEffect, useState } from "react";
import { listShares, deleteShare } from "@/api/share";
import { useNavigate } from "react-router-dom";
import DefaultLayout from "@/layouts/default";

export default function ProfilePage() {
  const [shares, setShares] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [deleting, setDeleting] = useState<string | null>(null);
  const navigate = useNavigate();

  useEffect(() => {
    setLoading(true);
    listShares()
      .then(res => {
        setShares(res.data.shares || []);
        setLoading(false);
      })
      .catch(() => {
        setError("Failed to load share links.");
        setLoading(false);
      });
  }, []);

  const handleDelete = async (id: string) => {
    if (!window.confirm("Are you sure you want to delete this share link?")) return;
    setDeleting(id);
    try {
      await deleteShare(id);
      setShares(shares.filter(s => s.id !== id));
    } catch {
      setError("Failed to delete share link.");
    } finally {
      setDeleting(null);
    }
  };

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-100/60 to-purple-100/60">
        <div className="glass p-8 w-full max-w-2xl shadow-lg">
          <h2 className="text-2xl font-bold mb-6 text-center">Share Management</h2>
          {loading ? (
            <div className="text-gray-500 text-center py-8">Loading...</div>
          ) : error ? (
            <div className="text-red-500 text-center py-8">{error}</div>
          ) : shares.length === 0 ? (
            <div className="text-gray-500 text-center py-8">No share links yet.</div>
          ) : (
            <ul className="space-y-4">
              {shares.map(share => (
                <li key={share.id} className="glass p-4 flex flex-col md:flex-row md:items-center md:justify-between">
                  <div>
                    <div className="font-semibold text-lg">{share.noteTitle || "Untitled Note"}</div>
                    <div className="text-xs text-gray-500">Expires: {share.expiry ? new Date(share.expiry).toLocaleString() : "Never"}</div>
                    <div className="text-xs text-blue-600 break-all mt-1">
                      <a href={`/share/${share.token}`} target="_blank" rel="noopener noreferrer">
                        {window.location.origin + "/share/" + share.token}
                      </a>
                    </div>
                  </div>
                  <div className="flex gap-2 mt-2 md:mt-0">
                    <button
                      className="px-3 py-1 rounded bg-red-500 hover:bg-red-600 text-white text-sm"
                      onClick={() => handleDelete(share.id)}
                      disabled={deleting === share.id}
                    >
                      {deleting === share.id ? "Deleting..." : "Delete"}
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          )}
          <div className="flex justify-end mt-6">
            <button
              className="px-3 py-1 rounded bg-gray-400 hover:bg-gray-500 text-white text-sm"
              onClick={() => navigate("/")}
            >
              Back to Home
            </button>
          </div>
        </div>
      </div>
    </DefaultLayout>
  );
} 