import React, { useState, useEffect, useRef } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { createNote, updateNote, getNotes } from "@/api/note";
import { uploadImage } from "@/api/image";
import { addToast } from "@heroui/toast";
import DefaultLayout from "@/layouts/default";

export default function NoteEditPage() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [loading, setLoading] = useState(false);
  const [imageUploading, setImageUploading] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    if (id) {
      setLoading(true);
      getNotes().then(res => {
        const note = (res.data.notes || []).find((n: any) => n.id === id);
        if (note) {
          setTitle(note.title);
          setContent(note.content);
        }
        setLoading(false);
      });
    }
  }, [id]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    try {
      if (id) {
        await updateNote({ id, title, content });
        addToast({
          title: "Success",
          description: "Note updated successfully.",
          color: "success",
        });
      } else {
        await createNote({ title, content });
        addToast({
          title: "Success",
          description: "Note created successfully.",
          color: "success",
        });
        setTitle("");
        setContent("");
      }
      navigate("/notes");
    } catch {
      addToast({
        title: "Error",
        description: "Failed to save note.",
        color: "danger",
      });
    } finally {
      setLoading(false);
    }
  };

  const handleImageUpload = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    setImageUploading(true);
    try {
      const url = await uploadImage(file);
      setContent(content + `\n![](${url})\n`);
    } catch {
      addToast({
        title: "Error",
        description: "Failed to upload image.",
        color: "danger",
      });
    } finally {
      setImageUploading(false);
    }
  };

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-100/60 to-purple-100/60">
        <div className="glass p-8 w-full max-w-2xl shadow-lg">
          <h2 className="text-2xl font-bold mb-6 text-center">{id ? "Edit Note" : "New Note"}</h2>
          <form onSubmit={handleSubmit} className="flex flex-col gap-4">
            <input
              type="text"
              placeholder="Title"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-blue-400"
              value={title}
              onChange={e => setTitle(e.target.value)}
              required
            />
            <textarea
              placeholder="Content (Markdown supported)"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-blue-400 min-h-[180px]"
              value={content}
              onChange={e => setContent(e.target.value)}
              required
            />
            <div className="flex items-center gap-4">
              <button
                type="button"
                className="px-3 py-1 rounded bg-purple-500 hover:bg-purple-600 text-white text-sm"
                onClick={() => fileInputRef.current?.click()}
                disabled={imageUploading}
              >
                {imageUploading ? "Uploading..." : "Upload Image"}
              </button>
              <input
                type="file"
                accept="image/*"
                className="hidden"
                ref={fileInputRef}
                onChange={handleImageUpload}
              />
              <span className="text-xs text-gray-500">Insert image into markdown</span>
            </div>
            <button
              type="submit"
              className="w-full py-2 mt-2 rounded bg-blue-500 hover:bg-blue-600 text-white font-semibold shadow"
              disabled={loading}
            >
              {loading ? "Saving..." : id ? "Update Note" : "Create Note"}
            </button>
          </form>
        </div>
      </div>
    </DefaultLayout>
  );
} 