import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import DefaultLayout from "@/layouts/default";
import { registerUser } from "@/api/user";
import { addToast } from "@heroui/toast";

export default function RegisterPage() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const navigate = useNavigate();

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!username || !password || !confirmPassword) {
      addToast({
        title: "Error",
        description: "Please fill in all fields.",
        color: "danger",
      });
      return;
    }
    if (password !== confirmPassword) {
      addToast({
        title: "Error",
        description: "Passwords do not match.",
        color: "danger",
      });
      return;
    }
    try {
      await registerUser({username, password});
      addToast({
        title: "Success",
        description: "Registration successful! You can now log in.",
        color: "success",
      });
      navigate("/login");
    } catch (err: any) {
      addToast({
        title: "Error",
        description: err?.response?.data?.message || "Registration failed. Please try again later.",
        color: "danger",
      });
    }
  };

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center h-[calc(100vh-4rem)] bg-gradient-to-br from-pink-100/60 to-blue-100/60">
        <div className="glass p-8 w-full max-w-md shadow-lg">
          <h2 className="text-2xl font-bold mb-6 text-center">Sign Up</h2>
          <form onSubmit={handleSubmit} className="flex flex-col gap-4">
            <input
              type="text"
              placeholder="Username"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-pink-400"
              value={username}
              onChange={e => setUsername(e.target.value)}
              autoComplete="username"
            />
            <input
              type="password"
              placeholder="Password"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-pink-400"
              value={password}
              onChange={e => setPassword(e.target.value)}
              autoComplete="new-password"
            />
            <input
              type="password"
              placeholder="Confirm Password"
              className="px-4 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-pink-400"
              value={confirmPassword}
              onChange={e => setConfirmPassword(e.target.value)}
              autoComplete="new-password"
            />
            <button
              type="submit"
              className="w-full py-2 mt-2 rounded bg-pink-500 hover:bg-pink-600 text-white font-semibold shadow"
            >
              Sign Up
            </button>
          </form>
          <div className="mt-4 text-center text-sm text-gray-700">
            Already have an account?{' '}
            <Link to="/login" className="text-pink-600 hover:underline">Sign In</Link>
          </div>
        </div>
      </div>
    </DefaultLayout>
  );
} 