import { Route, Routes } from "react-router-dom";

import IndexPage from "@/pages/index";
import LoginPage from "@/pages/login";
import RegisterPage from "@/pages/register";
import NotesPage from "@/pages/notes";
import NoteEditPage from "@/pages/note-edit";
import NoteDetailPage from "@/pages/note-detail";
import SharePage from "@/pages/share";
import ShareManagePage from "@/pages/shares";

function App() {
  return (
    <Routes>
      <Route element={<IndexPage />} path="/" />
      <Route element={<LoginPage />} path="/login" />
      <Route element={<RegisterPage />} path="/register" />
      <Route element={<NotesPage />} path="/notes" />
      <Route element={<NoteEditPage />} path="/notes/new" />
      <Route element={<NoteEditPage />} path="/notes/:id/edit" />
      <Route element={<NoteDetailPage />} path="/notes/:id" />
      <Route element={<SharePage />} path="/share/:token" />
      <Route element={<ShareManagePage />} path="/shares" />
    </Routes>
  );
}

export default App;
