import api from "./index";

export async function createShare(data: { noteId: string; expiresIn: number }) {
  return api.post("/api/share/create", {
    note_id: data.noteId,
    expire_in: data.expiresIn,
  });
}

export async function getShare(token: string) {
  return api.get(`/api/share/${token}`);
}

export async function listShares() {
  return api.get("/api/share/list");
}

export async function deleteShare(id: string) {
  return api.post("/api/share/delete", { id });
}

export async function reportShare(data: { token: string; reason: string }) {
  return api.post("/api/share/report", data);
}