import { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getShare, reportShare } from "@/api/share";
import ReactMarkdown from "react-markdown";
import DefaultLayout from "@/layouts/default";
import { addToast } from "@heroui/toast";

export default function SharePage() {
  const { token } = useParams();
  const navigate = useNavigate();
  const [note, setNote] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [showReportModal, setShowReportModal] = useState(false);
  const [reportReason, setReportReason] = useState("");
  const [reporting, setReporting] = useState(false);

  useEffect(() => {
    setLoading(true);
    getShare(token as string)
      .then(res => {
        setNote(res.data);
        setLoading(false);
      })
      .catch((err) => {
        setError(err?.response?.data?.error || "Failed to load shared note.");
        setLoading(false);
      });
  }, [token]);

  const handleReport = async () => {
    if (!reportReason.trim()) {
      addToast({
        title: "Error",
        description: "Please provide a reason for reporting.",
        color: "danger",
      });
      return;
    }

    setReporting(true);
    try {
      // extract token from url
      let shareToken = window.location.pathname.split('/').pop() || '';
      if (!shareToken) {
        addToast({
          title: "Error",
          description: "Invalid share link.",
          color: "danger",
        });
        return;
      }
      await reportShare({
        token: shareToken,
        reason: reportReason.trim()
      });
      addToast({
        title: "Success",
        description: "Report submitted successfully. Thank you for your feedback.",
        color: "success",
      });
      setShowReportModal(false);
      setReportReason("");
    } catch (err: any) {
      addToast({
        title: "Error",
        description: err?.response?.data?.error || "Failed to submit report. Please try again later.",
        color: "danger",
      });
    } finally {
      setReporting(false);
    }
  };

  if (loading) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)]">Loading...</div></DefaultLayout>;
  if (error) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)] text-red-500">{error}</div></DefaultLayout>;
  if (!note) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)] text-gray-500">Note not found or not shared.</div></DefaultLayout>;

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-100/60 to-purple-100/60">
        <div className="glass p-8 w-full max-w-2xl shadow-lg">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-2xl font-bold">{note.title}</h2>
            <div className="text-xs text-gray-500">{note.createdAt ? new Date(note.createdAt).toLocaleString() : ""}</div>
          </div>
          <div className="prose prose-blue max-w-none mb-6">
            {/* @ts-ignore */}
            <ReactMarkdown>{note.content}</ReactMarkdown>
          </div>
          <div className="flex justify-end gap-2">
            <button
              className="px-3 py-1 rounded bg-red-500 hover:bg-red-600 text-white text-sm"
              onClick={() => setShowReportModal(true)}
            >
              Report
            </button>
            <button
              className="px-3 py-1 rounded bg-gray-400 hover:bg-gray-500 text-white text-sm"
              onClick={() => navigate("/")}
            >
              Back to Home
            </button>
          </div>
        </div>
      </div>

      {/* Report Modal */}
      {showReportModal && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg p-6 w-full max-w-md mx-4">
            <h3 className="text-lg font-semibold mb-4">Report Shared Note</h3>
            <p className="text-gray-600 mb-4">
              Please provide a reason for reporting this shared note. Your report will help us maintain a safe environment.
            </p>
            <textarea
              className="w-full p-3 border border-gray-300 rounded-lg resize-none h-32 focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="Please describe the reason for reporting..."
              value={reportReason}
              onChange={(e) => setReportReason(e.target.value)}
              disabled={reporting}
            />
            <div className="flex justify-end gap-2 mt-4">
              <button
                className="px-4 py-2 rounded bg-gray-300 hover:bg-gray-400 text-gray-700"
                onClick={() => {
                  setShowReportModal(false);
                  setReportReason("");
                }}
                disabled={reporting}
              >
                Cancel
              </button>
              <button
                className="px-4 py-2 rounded bg-red-500 hover:bg-red-600 text-white disabled:opacity-50"
                onClick={handleReport}
                disabled={reporting || !reportReason.trim()}
              >
                {reporting ? "Submitting..." : "Submit Report"}
              </button>
            </div>
          </div>
        </div>
      )}
    </DefaultLayout>
  );
} 