import { useState, useEffect } from "react";
import { useAuth } from "@/provider";
import { useNavigate } from "react-router-dom";
import { getNotes, deleteNote } from "@/api/note";
import DefaultLayout from "@/layouts/default";

export default function NotesPage() {
  const { token } = useAuth();
  const navigate = useNavigate();
  const [notes, setNotes] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    if (!token) return;
    setLoading(true);
    getNotes()
      .then(res => {
        setNotes(res.data || []);
        setLoading(false);
      })
      .catch((err) => {
        setError(err?.response?.data?.error || "Failed to load notes.");
        setLoading(false);
      });
  }, [token]);

  const handleDelete = async (id: string) => {
    if (!window.confirm("Are you sure you want to delete this note?")) return;
    try {
      await deleteNote(id);
      setNotes(notes.filter(n => n.id !== id));
    } catch {
      setError("Failed to delete note.");
    }
  };

  return (
    <DefaultLayout>
      <div className="flex flex-col items-center justify-center min-h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-100/60 to-purple-100/60">
        <div className="glass p-8 w-full max-w-2xl shadow-lg">
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-bold">My Notes</h2>
            <button
              className="px-4 py-2 rounded bg-blue-500 hover:bg-blue-600 text-white font-semibold shadow"
              onClick={() => navigate("/notes/new")}
            >
              New Note
            </button>
          </div>
          {loading ? (
            <div className="text-gray-500 text-center py-8">Loading...</div>
          ) : error ? (
            <div className="text-red-500 text-center py-8">{error}</div>
          ) : notes.length === 0 ? (
            <div className="text-gray-500 text-center py-8">No notes yet.</div>
          ) : (
            <ul className="space-y-4">
              {notes.map(note => (
                <li key={note.id} className="glass p-4 flex flex-col md:flex-row md:items-center md:justify-between">
                  <div>
                    <div className="font-semibold text-lg">{note.title}</div>
                    <div className="text-xs text-gray-500">{note.createdAt ? new Date(note.createdAt).toLocaleString() : ""}</div>
                  </div>
                  <div className="flex gap-2 mt-2 md:mt-0">
                    <button
                      className="px-3 py-1 rounded bg-blue-500 hover:bg-blue-600 text-white text-sm"
                      onClick={() => navigate(`/notes/${note.id}`)}
                    >
                      View
                    </button>
                    <button
                      className="px-3 py-1 rounded bg-green-500 hover:bg-green-600 text-white text-sm"
                      onClick={() => navigate(`/notes/${note.id}/edit`)}
                    >
                      Edit
                    </button>
                    <button
                      className="px-3 py-1 rounded bg-red-500 hover:bg-red-600 text-white text-sm"
                      onClick={() => handleDelete(note.id)}
                    >
                      Delete
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          )}
        </div>
      </div>
    </DefaultLayout>
  );
} 