import { Button } from "@heroui/button";
import { Link } from "@heroui/link";
import {
  Navbar as HeroUINavbar,
  NavbarBrand,
  NavbarContent,
  NavbarItem,
} from "@heroui/navbar";

import { useAuth } from "@/provider";
import { useNavigate } from "react-router-dom";

export const Navbar = () => {
  const { token, user, logout } = useAuth();
  const navigate = useNavigate();
  
  const handleLogout = () => {
    logout();
    navigate("/login");
  };

  return (
    <HeroUINavbar 
      maxWidth="xl" 
      position="sticky" 
      className="glass backdrop-blur-lg border-b border-white/20 shadow-lg fixed top-0 left-0 right-0 z-50"
    >
      <NavbarContent className="basis-1/5 sm:basis-full" justify="start">
        <NavbarBrand className="gap-3 max-w-fit">
          <Link
            className="flex justify-start items-center gap-2 text-xl font-bold bg-gradient-to-r from-blue-600 to-purple-600 bg-clip-text text-transparent"
            color="foreground"
            href="/"
          >
            <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center">
              <span className="text-white font-bold text-sm">r3</span>
            </div>
            r3note
          </Link>
        </NavbarBrand>
        <div className="hidden lg:flex gap-6 justify-start ml-8">
          {token && (
            <>
              <NavbarItem>
                <Link
                  color="foreground"
                  href="/notes"
                  className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
                >
                  Notes
                </Link>
              </NavbarItem>
              <NavbarItem>
                <Link
                  color="foreground"
                  href="/shares"
                  className="text-gray-700 hover:text-blue-600 transition-colors duration-200 font-medium"
                >
                  Shares
                </Link>
              </NavbarItem>
            </>
          )}
        </div>
      </NavbarContent>

      <NavbarContent className="hidden sm:flex basis-1/5 sm:basis-full" justify="end">
        <NavbarItem className="hidden md:flex gap-3 items-center">
          {token ? (
            <>
              {user && (
                <span className="text-sm text-gray-700 font-medium px-3 py-1 rounded bg-white/40">
                  {user.username}
                </span>
              )}
              <Button
                className="text-sm font-medium text-white bg-gradient-to-r from-red-500 to-pink-500 hover:from-red-600 hover:to-pink-600 transition-all duration-200"
                onClick={handleLogout}
                variant="flat"
                size="sm"
              >
                Logout
              </Button>
            </>
          ) : (
            <>
              <Button
                className="text-sm font-medium text-gray-700 bg-white/60 hover:bg-white/80 border border-gray-200/50 transition-all duration-200"
                onClick={() => navigate("/login")}
                variant="flat"
                size="sm"
              >
                Sign In
              </Button>
              <Button
                className="text-sm font-medium text-white bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 transition-all duration-200"
                onClick={() => navigate("/register")}
                variant="flat"
                size="sm"
              >
                Sign Up
              </Button>
            </>
          )}
        </NavbarItem>
      </NavbarContent>
    </HeroUINavbar>
  );
};
