import { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { getNote } from "@/api/note";
import ReactMarkdown from "react-markdown";
import { createShare } from "@/api/share";
import { addToast } from "@heroui/toast";
import DefaultLayout from "@/layouts/default";

export default function NoteDetailPage() {
  const { id } = useParams();
  const navigate = useNavigate();
  const [note, setNote] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [shareModal, setShareModal] = useState(false);
  const [expiresIn, setExpiresIn] = useState(24);
  const [shareLink, setShareLink] = useState("");
  const [shareLoading, setShareLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
    getNote(id || "")
      .then(res => {
        setNote(res.data);
        setLoading(false);
      })
      .catch(() => {
        setError("Failed to load note.");
        setLoading(false);
      });
  }, [id]);

  const handleShare = async () => {
    if (expiresIn <= 0) {
      addToast({
        title: "Error",
        description: "Expiry time must be greater than 0.",
        color: "danger",
      });
      return;
    }
    setShareLoading(true);
    try {
      const res = await createShare({ noteId: note.id, expiresIn });
      setShareLink(window.location.origin + "/share/" + res.data.token);
    } catch {
      addToast({
        title: "Error",
        description: "Failed to create share link.",
        color: "danger",
      });
    } finally {
      setShareLoading(false);
    }
  };

  if (loading) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)]">Loading...</div></DefaultLayout>;
  if (error) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)] text-red-500">{error}</div></DefaultLayout>;
  if (!note) return <DefaultLayout><div className="flex items-center justify-center h-[calc(100vh-4rem)] text-gray-500">Note not found.</div></DefaultLayout>;

  return (
    <DefaultLayout>
      <div className="flex items-center justify-center min-h-[calc(100vh-4rem)] bg-gradient-to-br from-blue-100/60 to-purple-100/60">
        <div className="glass p-8 w-full max-w-2xl shadow-lg">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-2xl font-bold">{note.title}</h2>
            <div className="text-xs text-gray-500">{note.createdAt ? new Date(note.createdAt).toLocaleString() : ""}</div>
          </div>
          <div className="prose prose-blue max-w-none mb-6">
            {/* @ts-ignore */}
            <ReactMarkdown>{note.content}</ReactMarkdown>
          </div>
          <div className="flex gap-2">
            <button
              className="px-3 py-1 rounded bg-green-500 hover:bg-green-600 text-white text-sm"
              onClick={() => navigate(`/notes/${note.id}/edit`)}
            >
              Edit
            </button>
            <button
              className="px-3 py-1 rounded bg-gray-400 hover:bg-gray-500 text-white text-sm"
              onClick={() => navigate("/notes")}
            >
              Back
            </button>
            <button
              className="px-3 py-1 rounded bg-purple-500 hover:bg-purple-600 text-white text-sm"
              onClick={() => setShareModal(true)}
            >
              Share
            </button>
          </div>
        </div>
        {shareModal && (
          <div className="fixed inset-0 flex items-center justify-center bg-black/40 z-50">
            <div className="glass p-6 w-full max-w-sm shadow-lg relative">
              <h3 className="text-lg font-bold mb-4">Share Note</h3>
              <label className="block mb-2 text-sm">Link Expiry (hours):</label>
              <input
                type="number"
                min={1}
                max={168}
                value={expiresIn}
                onChange={e => setExpiresIn(Number(e.target.value))}
                className="w-full px-3 py-2 rounded bg-white/60 focus:outline-none focus:ring-2 focus:ring-purple-400 mb-4"
              />
              <div className="flex gap-2 mb-2">
                <button
                  className="flex-1 py-2 rounded bg-purple-500 hover:bg-purple-600 text-white font-semibold"
                  onClick={handleShare}
                  disabled={shareLoading}
                >
                  {shareLoading ? "Generating..." : "Generate Link"}
                </button>
                <button
                  className="flex-1 py-2 rounded bg-gray-400 hover:bg-gray-500 text-white font-semibold"
                  onClick={() => setShareModal(false)}
                >
                  Back
                </button>
              </div>
              {shareLink && (
                <div className="mt-2 text-center break-all">
                  <div className="text-xs text-gray-500 mb-1">Share this link:</div>
                  <input
                    type="text"
                    value={shareLink}
                    readOnly
                    className="w-full px-2 py-1 rounded bg-gray-100 text-xs text-gray-700 mb-2"
                    onFocus={e => e.target.select()}
                  />
                  <button
                    className="px-2 py-1 rounded bg-blue-500 hover:bg-blue-600 text-white text-xs"
                    onClick={() => {navigator.clipboard.writeText(shareLink)}}
                  >Copy Link</button>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    </DefaultLayout>
  );
} 