import api from "./index";

export async function getNotes() {
  return api.get("/api/note/list");
}

export async function getNote(id: string) {
  return api.get(`/api/note/${id}`);
}

export async function createNote(data: { title: string; content: string; images?: string[] }) {
  return api.post("/api/note/create", data);
}

export async function updateNote(data: { id: string; title: string; content: string; images?: string[] }) {
  const payload: any = { id: data.id, title: data.title, content: data.content };
  if (data.images) payload.image_ids = data.images;
  return api.post("/api/note/edit", payload);
}

export async function deleteNote(id: string) {
  return api.post("/api/note/delete", { id });
}